import React from 'react';

import './styles.css';

export default function Device({ device, handleClick }) {
	function icon(device) {
		switch (device.type) {
			case 'door':
				return 'sensor_door';
			case 'window':
				return 'sensor_window';
			case 'light':
				return 'light';
			case 'sensor':
				return 'sensors';
			case 'camera':
				return 'videocam';
			case 'sprinkler':
				return 'water_drop';
			default:
				return 'sensors';
		}
	}

	function status(device) {
		switch (device.type) {
			case 'door':
			case 'window':
				return device.active ? 'Aberto' : 'Fechado';
			case 'camera':
				return device.active ? 'Disponível' : 'Indisponível';
			case 'light':
			case 'sensor':
			case 'sprinkler':
			default:
				return device.active ? 'Ligado' : 'Desligado';
		}
	}

	return (
		<div
			className={`device ${device.active ? 'active' : ''}`}
			onClick={() => handleClick(device)}
		>
			<span className="material-icons">{icon(device)}</span>

			<div className="device__info">
				<span className="device__name">{device.name}</span>
				<span className="device__status">{status(device)}</span>

				<ul className="device__statistics"></ul>
			</div>
		</div>
	);
}
