import React, { useState } from 'react';
import Device from './components/Device/';

import './App.css';
import Form from './components/Form';

function App() {
	const [data, setData] = useState([
		{ id: 1, type: 'door', name: 'Porta Garagem', active: false },
		{ id: 2, type: 'door', name: 'Porta Sala', active: false },
		{ id: 3, type: 'door', name: 'Porta Cozinha', active: false },
		{ id: 4, type: 'window', name: 'Janela Sala 1', active: false },
		{ id: 5, type: 'window', name: 'Janela Sala 2', active: false },
		{ id: 6, type: 'window', name: 'Janela Corredor', active: false },
		{ id: 7, type: 'window', name: 'Janela Cozinha', active: false },
		{ id: 8, type: 'light', name: 'Lâmpada Garagem', active: false },
		{ id: 9, type: 'light', name: 'Lâmpada Sala', active: true },
		{ id: 10, type: 'light', name: 'Lâmpada Cozinha', active: false },
		{ id: 11, type: 'light', name: 'Lâmpada Quarto 1', active: true },
		{ id: 12, type: 'light', name: 'Lâmpada Quarto 2', active: false },
		{ id: 13, type: 'light', name: 'Lâmpada Quarto 3', active: false },
		{ id: 14, type: 'sensor', name: 'Sensor Presença 1', active: false },
		{ id: 15, type: 'sensor', name: 'Sensor Presença 2', active: false },
		{ id: 16, type: 'camera', name: 'Câmera 1', active: true },
		{ id: 17, type: 'camera', name: 'Câmera 2', active: true },
		{ id: 18, type: 'camera', name: 'Câmera 3', active: true },
		{ id: 19, type: 'camera', name: 'Câmera 4', active: true },
		{ id: 20, type: 'sprinkler', name: 'Irrigador 1', active: false },
		{ id: 21, type: 'sprinkler', name: 'Irrigador 2', active: false },
	]);

	function handleActivation(device) {
		let data_ = [...data];
		data_.forEach((d) => {
			if (d.id === device.id) {
				d.active = !d.active;
			}
		});

		setData(data_);
	}

	function addDevice(type, name) {
		const device = {
			id: data.length + 1,
			name: name,
			type: type,
			active: false,
		};

		setData([...data, device]);
	}

	return (
		<>
			<div className="banner">
				<div className="banner__logo">
					<span className="logo__name">SHM</span>
					<span className="logo__text">Smart House Manager</span>
				</div>

				<div className="banner__author">
					<span>by </span>
					<a href="http://vauxgomes.github.io">vauxgomes</a>
				</div>
			</div>

			<div className="devices">
				<h2 className="devices__title">Painel de Dispositivos</h2>

				{/* Device */}
				{data.map((item, key) => (
					<Device device={item} key={key} handleClick={handleActivation} />
				))}
			</div>

			{/* <Form handleSubmit={addDevice} /> */}
		</>
	);
}

export default App;
